import React, { useState, useEffect } from "react";
import Navbar from "./Homescreen/Navbar";
import Hero from "./Homescreen/Hero";
import Services from "./Homescreen/Services";
import Contact from "./Homescreen/Contact";
import Footer from "./Homescreen/Footer";
import Projects from "./Homescreen/Projects";
import VisionStats from "./Homescreen/VisionStats";
import SliderSection from "./Homescreen/SliderSection";

const Home = () => {
  const [loading, setLoading] = useState(true);

  // Simulate content loading with a delay
  useEffect(() => {
    setTimeout(() => {
      setLoading(false); // Set loading to false after 2 seconds (simulated content load)
    }, 2000);
  }, []);

  return (
    <div className="bg-gray-50 min-h-screen">
      {/* Loader */}
      {loading ? (
        <div className="fixed inset-0 bg-white flex items-center justify-center z-50">
          <div className="loader border-t-4 border-blue-600 border-solid rounded-full w-16 h-16 animate-spin"></div>
        </div>
      ) : (
        <div>
          {/* Main content */}
          <Navbar />
          <SliderSection />
          <VisionStats />

          <Hero />
          <Services />
          <Projects />
          <Contact />
          <Footer />
        </div>
      )}
    </div>
  );
};

export default Home;
