import React from "react";
import { Link } from "react-router-dom";
import webdevoperimage from '../../images/web.png';
import uiuximage from '../../images/uiux.png';
import digitaliamge from '../../images/digitalmarketting.png';

const Projects = () => {
  const projects = [
    { id: 1, title: "Web Development", image: webdevoperimage },
    { id: 2, title: "UI/UX Design", image: uiuximage },
    { id: 3, title: "App Development", image: digitaliamge },
    // Add more projects as needed
  ];

  return (
    <div className="py-16">
      {/* Title for the Projects section */}
      <h2 className="text-center text-3xl font-bold mb-8 text-blue-600">
        Our Projects
      </h2>

      {/* Project Grid */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
        {projects.map((project) => (
          <Link to={`/project/${project.id}`} key={project.id}>
            <div className="relative group">
              <img
                src={project.image}
                alt={project.title}
                className="w-full h-[300px] object-cover rounded-lg shadow-lg group-hover:scale-105 transition-transform"
              />
              <div className="absolute bottom-4 left-4 text-black text-lg font-semibold">
                {project.title}
              </div>
              <div className="absolute top-1/2 left-4 right-4 transform -translate-y-1/2 text-black opacity-0 group-hover:opacity-100 transition-opacity duration-300">
                <p className="text-sm">
                  Click to see more details about the {project.title}.
                </p>
                <Link
                  to={`/project/${project.id}`}
                  className="inline-block mt-4 bg-blue-500 text-black py-2 px-4 rounded-lg hover:bg-blue-600 transition-colors"
                >
                  View More
                </Link>
              </div>
            </div>
          </Link>
        ))}
      </div>
    </div>
  );
};

export default Projects;
