import React, { useState } from "react";
import { useParams } from "react-router-dom";
import herodesign from '../../images/fodify/hero.png';
import mainpagedesign from '../../images/fodify/mainpage.png';
import orderdesign from '../../images/fodify/orderpage.png';

const ProjectPage = () => {
  const { id } = useParams();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalImage, setModalImage] = useState(null);

  const projectImages = {
    1: [herodesign, mainpagedesign, orderdesign], // Example images for web development
    2: [herodesign, mainpagedesign, orderdesign], // Example images for UI/UX
    3: [herodesign, mainpagedesign, orderdesign], // Example images for app development
  };

  const images = projectImages[id] || [];

  // Open the modal and set the clicked image
  const openModal = (image) => {
    setModalImage(image);
    setIsModalOpen(true);
  };

  // Close the modal
  const closeModal = () => {
    setIsModalOpen(false);
    setModalImage(null);
  };

  return (
    <div className="py-16">
      <h2 className="text-center text-3xl font-bold mb-8">Project Images</h2>
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-8 mt-8">
        {images.map((image, index) => (
          <div key={index} onClick={() => openModal(image)} className="cursor-pointer">
            <img
              src={image}
              alt={`Project ${id} Image ${index + 1}`}
              className="w-full h-[300px] object-cover rounded-lg"
            />
          </div>
        ))}
      </div>

      {/* Modal to view image in full size */}
      {isModalOpen && (
        <div
          className="fixed top-0 left-0 w-full h-full bg-black bg-opacity-50 flex justify-center items-center"
          onClick={closeModal}
        >
          <div className="relative bg-white p-4 rounded-lg max-w-4xl w-full">
            {/* Image in Modal */}
            <img
              src={modalImage}
              alt="Modal Image"
              className="w-full h-[500px] object-contain"
            />

            {/* Close Button */}
            <button
              className="absolute top-2 right-2 text-white text-3xl font-bold"
              onClick={closeModal}
            >
              X
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default ProjectPage;
