import React from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";  // Import routing components
import Home from './Component/Home';
import ProjectPage from './Component/Homescreen/ProjectPage';  // Assuming you have a ProjectPage component

import './App.css';
import 'font-awesome/css/font-awesome.min.css';

function App() {
  return (
    <Router>
      <Routes>
        {/* Define the routes */}
        <Route path="/" element={<Home />} />
        <Route path="/project/:id" element={<ProjectPage />} /> {/* Route for each project */}
      </Routes>
    </Router>
  );
}

export default App;
