import React from "react";
import Slider from "react-slick";
import { motion } from "framer-motion";
import webdevoperimage from '../../images/web.png';
import uiuximage from '../../images/uiux.png';
import digitaliamge from '../../images/digitalmarketting.png';



const SliderSection = () => {
  const sliderData = [
    {
      id: 1,
      image: webdevoperimage,
      caption: "Innovative Web Development",
      description: "Transform your online presence with cutting-edge web development solutions tailored to your business needs.",
    },
    {
      id: 2,
      image: digitaliamge,
      caption: "Creative Digital Marketing",
      description: "Boost your brand visibility with creative marketing strategies that engage and convert your audience.",
    },
    {
      id: 3,
      image: uiuximage,
      caption: "UX/UI Design Excellence",
      description: "Deliver exceptional user experiences with intuitive, visually appealing design that drives customer satisfaction.",
    },
  ];

  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
  };

  return (
    <section className="py-16 bg-gradient-to-br from-blue-50 to-blue-100">
      <div className="container mx-auto px-6 md:px-12 lg:px-16 text-center">
        {/* Slider */}
        <Slider {...settings} className="w-full">
          {sliderData.map((slide) => (
            <motion.div
              key={slide.id}
              initial={{ opacity: 0, y: 50 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 1 }}
              className="relative !flex flex-col md:flex-row items-center justify-center"
            >
              {/* Image Section */}
              <div className="w-full md:w-1/2">
                <img
                  src={slide.image}
                  alt={`Slider ${slide.id}`}
                  className="w-full h-[400px] md:h-[500px] object-cover rounded-lg"
                />
              </div>

              {/* Text Section */}
              <div className="w-full md:w-1/2 px-8 mt-4 md:mt-0 text-center md:text-left">
                <h2 className="text-blue-600 text-3xl md:text-4xl font-bold mb-4">
                  {slide.caption}
                </h2>
                <p className="text-gray-800 text-lg">{slide.description}</p>
              </div>
            </motion.div>
          ))}
        </Slider>
      </div>
    </section>
  );
};

export default SliderSection;
