import React from "react";
import CountUp from "react-countup";
import { motion } from "framer-motion";

const VisionStats = () => {
  const stats = [
    { id: 1, title: "Potential Clients Reached", value: 100, suffix: "+" },
    { id: 2, title: "Tech Tools Mastered", value: 20, suffix: "+" },
    { id: 3, title: "Ideas in Development", value: 5, suffix: "+" },
  ];

  return (
    <section className="py-16 bg-gradient-to-br from-blue-50 to-blue-100 relative">
      <div className="container mx-auto px-6 md:px-12 lg:px-16 text-center">
        {/* Startup Title */}
        <motion.h1
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 1 }}
          className="text-5xl md:text-6xl font-bold text-gray-900 mb-6"
        >
          Welcome to <span className="text-blue-600">Celeronix</span>
        </motion.h1>

        {/* Vision & Ambitions Title */}
        <motion.h2
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 1 }}
          className="text-4xl md:text-5xl font-bold text-gray-900 mb-12"
        >
          Our Ambitions
        </motion.h2>

        {/* Stats Grid */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          {stats.map((stat, index) => (
            <motion.div
              key={stat.id}
              initial={{ opacity: 0, scale: 0.8 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{ duration: 0.6, delay: index * 0.2 }}
              className="p-6 bg-white rounded-lg shadow-lg flex flex-col items-center transform hover:scale-105 transition-transform"
            >
              <CountUp
                start={0}
                end={stat.value}
                duration={3}
                suffix={stat.suffix || ""}
                className="text-5xl font-bold text-blue-600"
              />
              <p className="text-lg text-gray-700 mt-3">{stat.title}</p>
            </motion.div>
          ))}
        </div>
      </div>

      {/* Decorative Animations */}
      <motion.div
        initial={{ opacity: 0, scale: 0 }}
        animate={{ opacity: 0.15, scale: 1 }}
        transition={{ duration: 3, repeat: Infinity, repeatType: "reverse" }}
        className="absolute -top-10 right-10 w-40 h-40 bg-blue-300 rounded-full blur-3xl"
      ></motion.div>
      <motion.div
        initial={{ opacity: 0, scale: 0 }}
        animate={{ opacity: 0.15, scale: 1 }}
        transition={{ duration: 3, repeat: Infinity, repeatType: "reverse", delay: 1 }}
        className="absolute bottom-10 left-10 w-40 h-40 bg-blue-400 rounded-full blur-3xl"
      ></motion.div>
    </section>
  );
};

export default VisionStats;
